<template>
  <div class="order-info">
    <div style="display: flex; align-items: flex-start; justify-content: space-between; flex-direction: column;">
      <span v-if="orderStatus !== statuses.PENDING">{{$t('orders.info.form.orderId')}}: {{order.id}}</span>
      <div v-else style="display: flex; flex-direction: row; justify-content: space-between; align-items: center;">
        <a :href="getExplorerUrl('tx', order.hash)" target="_blank" class="link-a">{{isMobile ? formatAddress(order.hash) : order.hash}}</a>
        <img src="../../assets/blue-loader.249140da.svg" alt="loader" style="margin-left: 5px;" class="tx-list-status-img loading-img">
      </div>

      <div class="order-oper-panel">
        <el-button v-if="canOperate(opers.delivered)" :loading="txPending" size="mini" type="primary" round @click="goOperate(opers.delivered)">
          {{$t('orders.info.btn.delivered')}}
          {{needApprove ? `(${$t('common.approve')})` : ''}}
        </el-button>
        <el-button v-if="canOperate(opers.frozeOrder)" :loading="txPending" size="mini" type="primary" round @click="goOperate(opers.frozeOrder)">
          {{$t('orders.info.btn.frozeOrder')}}
          {{needApprove ? `(${$t('common.approve')})` : ''}}
        </el-button>
        <el-button v-if="canOperate(opers.confirmReceived)" :loading="txPending" size="mini" type="primary" round @click="goOperate(opers.confirmReceived)">{{$t('orders.info.btn.confirmReceived')}}</el-button>
        <el-button v-if="canOperate(opers.confirmNotDelivered)" :loading="txPending" size="mini" type="primary" round @click="goOperate(opers.confirmNotDelivered)">{{$t('orders.info.btn.confirmNotDelivered')}}</el-button>
        <el-button v-if="canOperate(opers.withdrawTimeoutOrder)" :disabled="remainTimer > 0" :loading="txPending" round size="mini" type="primary" @click="goOperate(opers.withdrawTimeoutOrder)">{{$t('orders.info.btn.withdrawTimeoutOrder')}}{{remainWindowTime}}</el-button>
      </div>
    </div>
    <div style="margin-top: 10px;">
      <el-descriptions :column="isMobile ? 1 : 2" size="mini">
        <el-descriptions-item :label="$t('orders.info.form.payer')">
          <a class="link-a" :href="getExplorerUrl('address', orderForm.payer)" target="_blank">
            {{isMobile ? formatAddress(orderForm.payer) : orderForm.payer}}
          </a>
          <i class="el-icon-document-copy btn-icon" style="margin-left: 5px;" @click="copyText(orderForm.payer)"></i>
        </el-descriptions-item>
        <el-descriptions-item :label="$t('orders.info.form.payee')">
          <a class="link-a" :href="getExplorerUrl('address', orderForm.payee)" target="_blank">
            {{isMobile ? formatAddress(orderForm.payee) : orderForm.payee}}
          </a>
          <i class="el-icon-document-copy btn-icon" style="margin-left: 5px;" @click="copyText(orderForm.payee)"></i>
        </el-descriptions-item>
        <el-descriptions-item v-if="isSender" :label="$t('orders.info.form.amount')">
          {{orderAmount}} {{tokenSymbol}}
        </el-descriptions-item>
        <el-descriptions-item v-if="isSender" :label="$t('orders.info.form.fee')">
          {{feeAmount}} {{tokenSymbol}}
        </el-descriptions-item>
        <el-descriptions-item v-if="isSender && orderStatus === statuses.SUCCESS" :label="$t('orders.info.form.dToken')">
          {{dtokenAmount}} {{dtokenSymbol}}
        </el-descriptions-item>
        <el-descriptions-item v-if="!isSender" :label="$t('orders.info.form.actuallyArrived')">
          {{orderAmount - feeAmount}} {{tokenSymbol}}
        </el-descriptions-item>
        <el-descriptions-item :label="$t('orders.info.form.margin')">
          {{marginAmount}} {{tokenSymbol}}
        </el-descriptions-item>
        <el-descriptions-item :label="$t('orders.info.form.modifyTime')">
          {{lastModifyTime}}
        </el-descriptions-item>
        <el-descriptions-item :label="$t('orders.info.form.windowHour')">
          {{orderForm.windowHour}} {{remainWindowTime}}
        </el-descriptions-item>
        <el-descriptions-item :label="$t('orders.info.form.status')">
          {{orderStatus}}
        </el-descriptions-item>
      </el-descriptions>
    </div>
    <el-dialog
        :visible.sync="txConfirmDialogVisible"
        append-to-body
        custom-class="wallet-dialog"
        :title="$t('common.txConfirmDialogTitle')"
        width="90%">
      <tx-confirm ref="txConfirm" v-model="hash" :tx-form="txForm" :call-func="txConfirmCall">
        <div style="width: 100%;" slot="content">
          <div v-if="txForm.type === approveTxType" style="width: 100%; text-align: center;">
            {{$t('common.approve')}} {{tokenSymbol}}
          </div>
          <div v-else class="sec_form_panel">
            <div class="sec_form_item">
              <div class="sec_form_item_title">
                <span>{{$t('orders.info.form.orderId')}}:</span>
              </div>
              <div class="sec_form_item_content">
                {{txForm.orderId}}
              </div>
            </div>

            <div class="sec_form_item">
              <div class="sec_form_item_title">
                <span>{{$t('orders.info.form.operate')}}:</span>
              </div>
              <div class="sec_form_item_content">
                {{txForm.operateInfo}}
              </div>
            </div>

            <div v-if="needMargin" class="sec_form_item">
              <div class="sec_form_item_title">
                <span>{{$t('orders.info.form.margin')}}:</span>
              </div>
              <div class="sec_form_item_content">
                {{marginAmount}} {{tokenSymbol}}
              </div>
            </div>

            <div v-if="operateTip !== ''" class="sec_form_item">
              <div class="sec_form_item_title">
                <span>{{$t('common.tip')}}:</span>
              </div>
              <div class="sec_form_item_content">
                {{operateTip}}
              </div>
            </div>
          </div>
        </div>
        <span slot="info">{{txForm.operateInfo}}</span>
      </tx-confirm>
    </el-dialog>
  </div>
</template>
<script>
import common from '@/utils/common'
import TxConfirm from '../tx/tx_confirm'
import pool from "@/utils/pool";
import events from "@/utils/events"
import base from "@/utils/base"
import tx_base from "@/utils/tx_base"
import order_base from "@/utils/order_base"
import erc20 from "@/utils/erc20";
export default {
  mixins: [base, tx_base, order_base],
  name: 'OrderIntro',
  props: {
    order: {
      type: Object,
    }
  },
  components: {
    TxConfirm
  },
  computed: {
    approveTxType() {
      return common.getTxTypes().approve
    },
    isSender() {
      return this.order.payer.toLowerCase() === this.account.toLowerCase()
    },
    orderAmount() {
      return common.weiToUnit(this.orderForm.amount)
    },
    marginAmount() {
      return common.weiToUnit(this.orderForm.margin)
    },
    feeAmount() {
      return common.weiToUnit(this.orderForm.fee)
    },
    dtokenAmount() {
      return common.weiToUnitWithDecimals(this.orderForm.dToken, this.dtokenDecimals)
    },
    orderStatus() {
      if (!this.orderForm.id) {
        return this.statuses.PENDING
      }
      return common.hexToString(this.orderForm.status)
    },
    orderRole () {
      if (this.account.toLowerCase() === this.orderForm.payer.toLowerCase()) {
        return this.roles.sender
      } else {
        return this.roles.receiver
      }
    },
    lastModifyTime() {
      if (this.orderStatus === this.statuses.PENDING) {
        return ''
      }
      return new Date(this.orderForm.modifyTime.toNumber() * 1000).toLocaleString()
    },
    operateTip() {
      if (this.orderStatus === this.statuses.FROZE &&
          (this.txForm.type === common.getTxTypes().confirmReceived || this.txForm.type === common.getTxTypes().confirmNotDelivered)) {
        if (this.orderForm.margin.gt(common.toBigNumber(0))) {
          const loseAmountWei = this.orderForm.margin.mul(common.toBigNumber(10)).div(common.toBigNumber(100))
          const loseAmount = common.weiToUnit(loseAmountWei)
          return `${this.$t('orders.info.form.punishTip')} (${loseAmount} ${this.tokenSymbol})`
        }
      }
      return ''
    },
    needMargin() {
      if (this.isSender) {
        return false
      }
      if (this.txForm.type === common.getTxTypes().delivered ||
          (this.orderStatus === this.statuses.INIT && this.txForm.type === common.getTxTypes().frozeOrder)) {
        return true
      }
      return false
    },
    needApprove() {
      if (this.isSender) {
        return false
      }
      if (this.tokenAllowance === null) {
        return false
      }
      if (this.orderForm.margin === '' || !this.orderForm.margin) {
        return false
      }
      if (this.tokenAllowance.lt(this.orderForm.margin)) {
        return true
      } else {
        return false
      }
    },
  },
  data() {
    return {
      orderForm: {
        id: null,
        amount: 0,
        margin: 0,
        fee: 0,
        dToken: 0,
        inviter: '',
        modifyTime: 0,
        payee: '',
        payer: '',
        status: '0x',
        windowHour: 0,
        windowHeight: 0,
      },
      remainWindowTime: '',
      remainTimer: 0,
      interval: null,
      loading: false,
      txConfirmDialogVisible: false,
    }
  },
  created() {

    const txConfirmedSuperHandler = this.eventHandlers[events.txConfirmed]
    this.eventHandlers[events.txConfirmed] = params => {
      if (txConfirmedSuperHandler) {
        txConfirmedSuperHandler(params)
        if (this.hash === params.hash) {
          this.getOrder()
        }
      }
    }
    this.init()
  },
  watch: {
    remainTimer() {
      this.computeRemainWindowTime()
    }
  },
  destroyed() {
  },
  methods: {
    init() {
      common.objCopy(this.order, this.orderForm)
      this.setOrderRemainSeconds()
    },
    setOrderRemainSeconds() {
      if (this.orderStatus === this.statuses.PENDING || this.orderStatus === this.statuses.SUCCESS || this.orderStatus === this.statuses.FAILED
          || this.orderStatus === this.statuses.FROZE) {
        return
      }
      const timePass = new Date().getTime() - this.orderForm.modifyTime.toNumber() * 1000
      const timeRemain = this.orderForm.windowHour.toNumber() * 3600 * 1000 - timePass
      const seconds = timeRemain / 1000
      if (seconds < 0) {
        this.remainWindowTime = ''
        return;
      }

      this.remainTimer = seconds
    },
    computeRemainWindowTime() {
      if (this.interval) {
        clearInterval(this.interval)
      }

      if (this.remainTimer <= 0) {
        this.remainWindowTime = ''
        return
      }

      if (this.orderStatus !== this.statuses.INIT && this.orderStatus !== this.statuses.DELIVERED) {
        this.remainWindowTime = ''
        return
      }

      const timeRemainText = this.formatSeconds(this.remainTimer)
      if (timeRemainText !== '') {
        let withdrawRole = ''
        if (this.isSender) {
          if (this.orderStatus === this.statuses.INIT) {
            withdrawRole = ''
          } else {
            withdrawRole = this.$t('common.counterparty')
          }
        } else {
          if (this.orderStatus === this.statuses.DELIVERED) {
            withdrawRole = ''
          } else {
            withdrawRole = this.$t('common.counterparty')
          }
        }

        this.remainWindowTime = `(${this.$t('common.remain')})`.replaceAll('%role%', withdrawRole).replaceAll('%time%', timeRemainText)
        this.interval = setInterval(() => {
          this.remainTimer -= 1
        }, 1000)
      } else {
        this.remainWindowTime = ''
      }
    },
    async getOrder() {
      this.loading = true
      const r = await pool.orders(this.order.id)
      this.orderForm = {
        id: this.order.id,
        amount: r.amount,
        margin: r.margin,
        fee: r.fee,
        dToken: r.dToken,
        inviter: r.inviter,
        modifyTime: r.modifyTime,
        payee: r.payee,
        payer: r.payer,
        status: r.status,
        windowHour: r.windowHour,
        windowHeight: r.windowHeight,
      }
      this.setOrderRemainSeconds()
      this.loading = false
    },
    canOperate(operateType) {
      if (this.orderStatus === this.statuses.PENDING) {
        return false
      }
      switch (operateType) {
        case this.opers.delivered:
          return this.orderRole === this.roles.receiver && this.orderStatus === this.statuses.INIT
        case this.opers.frozeOrder:
          return (this.orderRole === this.roles.receiver && this.orderStatus === this.statuses.INIT)
              || (this.orderRole === this.roles.sender && this.orderStatus === this.statuses.DELIVERED)
        case this.opers.confirmReceived:
          return this.orderRole === this.roles.sender &&
              (this.orderStatus === this.statuses.FROZE || this.orderStatus === this.statuses.INIT
                  || this.orderStatus === this.statuses.DELIVERED)
        case this.opers.confirmNotDelivered:
          return this.orderRole === this.roles.receiver && this.orderStatus === this.statuses.FROZE
        case this.opers.withdrawTimeoutOrder:
          return (this.orderRole === this.roles.receiver && this.orderStatus === this.statuses.DELIVERED)
              || (this.orderRole === this.roles.sender && this.orderStatus === this.statuses.INIT)
      }
      return false
    },
    goOperate(operateType) {
      if (this.$refs['txConfirm']) {
        this.$refs['txConfirm'].init()
      }
      switch (operateType) {
        case this.opers.delivered:

          if (this.orderForm.margin.gt(this.tokenBalance)) {
            this.$message.error(this.$t('orderNew.btn.marginNotEnough'))
            return
          }

          if (this.needApprove) {
            this.txForm = {
              type: common.getTxTypes().approve,
              token: this.tokenItem.address,
              to: this.poolItem.address,
              sender: this.account,
            }
            this.txConfirmCall = () => {
              return erc20.approve(this.txForm.to, -1)
            }
          } else {
            this.txForm = {
              type: common.getTxTypes().delivered,
              orderId: this.order.id.toString(),
              operateInfo: this.$t('txType.delivered')
            }
            this.txConfirmCall = () => {
              return pool.delivered(this.order.id)
            }
          }
          break
        case this.opers.frozeOrder:
          if (this.orderForm.margin.gt(this.tokenBalance)) {
            this.$message.error(this.$t('orderNew.btn.marginNotEnough'))
            return
          }

          if (this.needMargin && this.needApprove) {
            this.txForm = {
              type: common.getTxTypes().approve,
              token: this.tokenItem.address,
              to: this.poolItem.address,
              sender: this.account,
            }
            this.txConfirmCall = () => {
              return erc20.approve(this.txForm.to, -1)
            }
          } else {
            this.txForm = {
              type: common.getTxTypes().frozeOrder,
              orderId: this.order.id.toString(),
              operateInfo: this.$t('txType.frozeOrder')
            }
            this.txConfirmCall = () => {
              return pool.frozeOrder(this.order.id)
            }
          }
          break
        case this.opers.confirmReceived:
          this.txForm = {
            type: common.getTxTypes().confirmReceived,
            orderId: this.order.id.toString(),
            operateInfo: this.$t('txType.confirmReceived')
          }
          this.txConfirmCall = () => {
            return pool.confirmReceived(this.order.id)
          }
          break
        case this.opers.confirmNotDelivered:
          this.txForm = {
            type: common.getTxTypes().confirmNotDelivered,
            orderId: this.order.id.toString(),
            operateInfo: this.$t('txType.confirmNotDelivered')
          }
          this.txConfirmCall = () => {
            return pool.confirmNotDelivered(this.order.id)
          }
          break
        case this.opers.withdrawTimeoutOrder:
          this.txForm = {
            type: common.getTxTypes().withdrawTimeoutOrder,
            orderId: this.order.id.toString(),
            operateInfo: this.$t('txType.withdrawTimeoutOrder')
          }
          this.txConfirmCall = () => {
            return pool.withdrawTimeoutOrder(this.order.id)
          }
          break
      }
      this.txConfirmDialogVisible = true
    },
    formatSeconds(value) {
      let sec = parseInt(value)
      let min = 0
      let hour = 0
      let day = 0
      if (sec > 60) {
        min = parseInt(sec / 60)
        sec = parseInt(sec % 60)
        if (min > 60) {
          hour = parseInt(min / 60)
          min = parseInt(min % 60)
          if (hour > 24) {
            day = parseInt(hour / 24)
            hour = parseInt(hour % 24)
          }
        }
      }
      if (sec.toString().length < 2) {
        sec = '0' + sec.toString()
      }
      if (min.toString().length < 2) {
        min = '0' + min.toString()
      }
      if (hour.toString().length < 2) {
        hour = '0' + hour.toString()
      }
      let result = `${hour}:${min}:${sec}`
      if (day <= 0) {
        return result
      }
      return `${day}${this.$t('common.day')} ${result}`
      // let result = ''
      // if(sec > 0){
      //   result = ' ' + parseInt(sec) + '' + this.$t('common.second')
      // }
      // if(min > 0) {
      //   result = ' ' + parseInt(min) + this.$t('common.minute') + result
      // }
      // if(hour > 0) {
      //   result = ' ' + parseInt(hour) + this.$t('common.hour') + result
      // }
      // if(day > 0) {
      //   result = ' ' + parseInt(day) + this.$t('common.day') + result
      // }
      // return result
    },
  }
}
</script>
<style>
.order-info {
  padding: 10px;
  margin-top: 10px;
  border-radius: 10px;
  background-color: #3c3b3b;
}
.order-info .el-descriptions {
  /*background-color: #151414;*/
}
.order-info .el-descriptions__header {
  margin-bottom: 0!important;
}
.order-info .el-descriptions__body {
  background-color: inherit;
}
.order-oper-panel .el-button {
  margin-left: 5px;
  margin-top: 5px;
}
</style>
