<template>
  <div>
    <div style="display: flex; flex-direction: row; align-items: center">
      <el-button style="margin-left: 20px;" type="text" :disabled="!canSendTx" @click="onRefresh">{{$t('common.refresh')}}</el-button>
    </div>

    <div v-if="accountType === roles.sender && pendingCreateOrderList.length > 0" style="margin-top: 10px;">
      <order-intro v-for="item in pendingCreateOrderList" :key="item.hash" :order="item"></order-intro>
    </div>

    <div style="margin-top: 10px;" v-loading="loading">
      <div>
        <order-intro v-for="item in filterOrderList" :ref="'orderIntro_' + item.id.toString()" :key="item.id.toString() + '_' + item.status" :order="item"></order-intro>
      </div>
      <el-pagination style="margin-top: 10px;" background :page-size="pageSize"
                     layout="pager,slot" :total="orderSize" @current-change="onCurrentChange">
        <span>{{$t('common.total')}} {{orderSize}}</span>
      </el-pagination>
    </div>
  </div>
</template>
<script>
import OrderIntro from './order_intro'
import pool from "@/utils/pool"
import events from "@/utils/events"
import common from "@/utils/common"
import base from "@/utils/base"
import order_base from "@/utils/order_base"
export default {
  mixins: [base, order_base],
  name: 'OrderList',
  components: {
    OrderIntro
  },
  props: {
    accountType: {
      type: String,
    }
  },
  computed: {
    pendingCreateOrderList() {
      const txs = this.$store.state.transactions
      const list = []
      txs.forEach(item => {
        if (item.type === common.getTxTypes().create) {
          if (common.getTxStatus(item) === 'pending') {
            list.push(item)
          }
        }
      })
      return list
    },
    initCount() {
      let count = 0
      this.list.forEach(item => {
        if (common.hexToString(item.status) === this.statuses.INIT) {
          count++
        }
      })
      return count
    },
    deliveredCount() {
      let count = 0
      this.list.forEach(item => {
        if (common.hexToString(item.status) === this.statuses.DELIVERED) {
          count++
        }
      })
      return count
    },
    frozeCount() {
      let count = 0
      this.list.forEach(item => {
        if (common.hexToString(item.status) === this.statuses.FROZE) {
          count++
        }
      })
      return count
    },
    successCount() {
      let count = 0
      this.list.forEach(item => {
        if (common.hexToString(item.status) === this.statuses.SUCCESS) {
          count++
        }
      })
      return count
    },
    failedCount() {
      let count = 0
      this.list.forEach(item => {
        if (common.hexToString(item.status) === this.statuses.FAILED) {
          count++
        }
      })
      return count
    },
    filterOrderList() {
      if (this.checkStatus === '') {
        return this.list
      }

      const list = []
      this.list.forEach(item => {
        if (common.hexToString(item.status) === this.checkStatus) {
          list.push(item)
        }
      })

      return list
    },
  },
  watch: {
  },
  data() {
    return {
      loading: false,
      orderSize: 0,
      pageSize: 20,
      pageNum: 1,
      list: [],
      checkStatus: '',
    }
  },
  created() {

    const txConfirmedSuperHandler = this.eventHandlers[events.txConfirmed]
    this.eventHandlers[events.txConfirmed] = params => {
      if (txConfirmedSuperHandler) {
        txConfirmedSuperHandler(params)
      }
      if (params.type >= common.getTxTypes().create && params.type <= common.getTxTypes().confirmNotDelivered) {
        this.getList()
      }
    }

    this.eventHandlers[events.tokenChanged] = () => {
      this.getList()
    }
  },
  methods: {
    baseInit() {
      this.getList()
    },
    checkInti() {
      if (this.checkStatus === this.statuses.INIT) {
        this.checkStatus = ''
        return
      }
      this.checkStatus = this.statuses.INIT
    },
    checkDelivered() {
      if (this.checkStatus === this.statuses.DELIVERED) {
        this.checkStatus = ''
        return
      }
      this.checkStatus = this.statuses.DELIVERED
    },
    checkFroze() {
      if (this.checkStatus === this.statuses.FROZE) {
        this.checkStatus = ''
        return
      }
      this.checkStatus = this.statuses.FROZE
    },
    checkSuccess() {
      if (this.checkStatus === this.statuses.SUCCESS) {
        this.checkStatus = ''
        return
      }
      this.checkStatus = this.statuses.SUCCESS
    },
    checkFailed() {
      if (this.checkStatus === this.statuses.FAILED) {
        this.checkStatus = ''
        return
      }
      this.checkStatus = this.statuses.FAILED
    },
    async onRefresh() {
      await this.getList()
      this.$message.success(this.$t('common.operateSuccess'))
    },
    onCheckStatusChange() {
    },
    async onCurrentChange(val) {
      this.pageNum = val
      await this.getOrderList()
    },
    async getList() {
      if (null === this.poolItem) {
        return
      }
      if (this.account === '') {
        return
      }
      this.loading = true
      await this.getOrderSize()
      await this.getOrderList()
      this.loading = false
    },
    async getOrderSize() {
      let orderSizeTmp = 0
      if (this.accountType === this.roles.sender) {
        orderSizeTmp = await pool.payerOrderSize(this.account)
      } else if (this.accountType === this.roles.receiver) {
        orderSizeTmp = await pool.payeeOrderSize(this.account)
      }
      this.orderSize = orderSizeTmp.toNumber()
    },
    async getOrderList() {

      let from = this.orderSize - this.pageNum * this.pageSize
      let to = this.orderSize - (this.pageNum - 1) * this.pageSize
      if (from < 0) {
        from = 0
      }

      const tmpList = []
      if (this.orderSize > 0) {
        let orderList = []
        if (this.accountType === this.roles.sender) {
          orderList = await pool.getPayerOrders(this.account, from, to)
        } else if (this.accountType === this.roles.receiver) {
          orderList = await pool.getPayeeOrders(this.account, from, to)
        }

        for (let i = 0; i < orderList.length; i++) {
          const r = orderList[i]
          tmpList.push({
            id: r.id,
            amount: r.amount,
            margin: r.margin,
            fee: r.fee,
            dToken: r.dToken,
            inviter: r.inviter,
            modifyTime: r.modifyTime,
            payee: r.payee,
            payer: r.payer,
            status: r.status,
            windowHour: r.windowHour,
            windowHeight: r.windowHeight,
          })
        }

        tmpList.sort(this.orderSort())
      }

      this.list = tmpList
      this.list.forEach(item => {
        if (this.$refs['orderIntro_' + item.id.toString()] && this.$refs['orderIntro_' + item.id.toString()].length > 0) {
          this.$refs['orderIntro_' + item.id.toString()][0].init()
        }
      })
    },
    orderSort() {
      return (o1, o2) => {
        return o2.id - o1.id
      }
    }
  },
}
</script>
